import { context, getOctokit } from "@actions/github";
import { debug, endGroup, error, info, startGroup, warning } from "@actions/core";
import { getJiraUrl, getPRUrl } from "./utils";
class DryRunReleaser {
    createRelease(params) {
        info(`[DRYRUN] Create Release ${params.tag_name} on ${params.owner}/${params.repo} with params:
            ${JSON.stringify(params, null, 2)}
        `);
        return Promise.resolve({
            data: {
                id: 1,
                name: params.name,
                html_url: `http://happn.com/fake-release/${params.name}`,
                assets: [],
                target_commitish: params.target_commitish || "fakesha",
                draft: params.draft || false,
                prerelease: params.prerelease || false,
                tag_name: params.tag_name,
                body: params.body
            }
        });
    }
}
class GitHubReleaser {
    github;
    constructor(github) {
        this.github = github;
    }
    createRelease(params) {
        return this.github.rest.repos.createRelease(params);
    }
}
export function getMdChangelog(messages) {
    const replaceJira = (text) => text.replace(/([A-Z]+-[0-9]+)/g, `[$1](${getJiraUrl("$1")})`);
    const replaceGithubPr = (text) => text.replace(/#([0-9]+)/g, `[#$1](${getPRUrl("$1")})`);
    return messages
        .map(m => '*️ ' + m)
        .map(replaceJira)
        .map(replaceGithubPr)
        .join("\n");
}
export async function createRelease(config, tagName, changelog) {
    if (!config.github.enabled) {
        info("Github release disabled");
        return;
    }
    startGroup("🚀 Create Github Release");
    const releaser = config.dryRun ? new DryRunReleaser()
        : new GitHubReleaser(getOctokit(process.env.GITHUB_TOKEN || "", {
            log: {
                error: error,
                info: info,
                warn: warning,
                debug: debug
            }
        }));
    const releaseData = await releaser.createRelease({
        owner: context.repo.owner,
        repo: context.repo.repo,
        tag_name: tagName,
        name: tagName,
        body: changelog,
        draft: false,
        prerelease: false,
        generate_release_notes: false,
        discussion_category_name: undefined,
        target_commitish: undefined
    });
    info(`Created release ${tagName}`);
    endGroup();
    return releaseData.data;
}
